<template>
  <div class="modal is-active">
    <div class="card">
      <img
        src="./../../assets/Icon_x.svg"
        class="closeButton"
        @click="hideModal()"
      />
      <div class="downloadContainer">
        <div class="contentContainer">
          <h4>Télécharger au format JSON</h4>
          <p>
            Sauvegarde tous les filtres sélectionnés dans un fichier JSON afin
            de continuer vos recherches plus tard en le téléversant sur ce site.
          </p>
          <button @click="downloadJSON()">
            <div>
              <span>JSON</span>
              <img src="./../../assets/icon_download-invert.svg" alt="" />
            </div>
          </button>
        </div>
        <div class="contentContainer">
          <h4>Sauvegarder au format document</h4>
          <p>
            Permet de sauvegarder votre recherche et de la mettre au propre dans
            un fichier document.
          </p>
          <button @click="downloadDocument()">
            <div>
              <span>DOC</span>
              <img src="./../../assets/icon_download-invert.svg" alt="" />
            </div>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Document } from "docx";
import { Packer, Paragraph, HeadingLevel } from "docx";
import { saveAs } from "file-saver";

export default {
  methods: {
    hideModal() {
      this.$root.$refs.headerContent.showDownloadModal();
    },
    downloadDocument() {
      let domains = this.$root.$refs.domainList.filteredDomains;
      let dataSections = [
        {
          children: [],
        },
      ];
      let title = new Paragraph({
        text: "Résultats de votre recherche :",
        heading: HeadingLevel.HEADING_1,
      });
      dataSections[0].children.push(title);
      domains.forEach((domain) => {
        if (domain.jobs.length > 0) {
          let domainName = new Paragraph({
            text: domain.name,
            heading: HeadingLevel.HEADING_2,
          });
          let domainDescription = new Paragraph({
            text: domain.description,
          });
          dataSections[0].children.push(domainName, domainDescription);
          if (domain.tasks.length > 0) {
            let taskLabel = new Paragraph({
              text: "Tâches",
              heading: HeadingLevel.HEADING_3,
            });
            dataSections[0].children.push(taskLabel);
            domain.tasks.forEach((task) => {
              let taskName = new Paragraph({
                text: task,
                bullet: {
                  level: 0,
                },
              });
              dataSections[0].children.push(taskName);
            });
          }
          if (domain.jobs.length > 0) {
            let jobLabel = new Paragraph({
              text: "Emplois",
              heading: HeadingLevel.HEADING_3,
            });
            dataSections[0].children.push(jobLabel);
            domain.jobs.forEach((job) => {
              let jobName = new Paragraph({
                text: job.name,
                bullet: {
                  level: 0,
                },
              });
              dataSections[0].children.push(jobName);
            });
          }
        }
      });
      let doc = new Document({
        sections: dataSections,
      });
      Packer.toBlob(doc).then((blob) => {
        saveAs(blob, "Cheminement-Carrière.docx");
      });
    },
    downloadJSON() {
      let saveFile = {
        domains: this.$root.$refs.domainList.filteredDomains,
        conditions: this.$root.$refs.conditionList.selectedConditions,
        interests: this.$root.$refs.interestList.selectedInterests,
        types: this.$root.$refs.sortingList.selectedTypes,
      };
      let json = JSON.stringify(saveFile);
      let blob = new Blob([json], { type: "application/json" });
      let url = window.URL.createObjectURL(blob);
      let link = document.createElement("a");
      link.href = url;
      link.download = "Cheminement-Carrière.json";
      link.click();
      window.URL.revokeObjectURL(url);
    },
  },
};
</script>

<style scoped>
img {
  padding-left: 6px;
}
button div {
  width: 60%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: auto;
}
button {
  width: 150px;
}
.downloadContainer {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.contentContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  min-height: 200px;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1500;
}
.card {
  box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  margin: 30px;
  border-radius: 5px;
  border: 1px solid rgb(232, 232, 232);
  padding: 30px;
  background-color: white;
}
.closeButton {
  float: right;
}
.closeButton:hover {
  cursor: pointer;
  filter: invert(37%) sepia(95%) saturate(3013%) hue-rotate(334deg)
    brightness(98%) contrast(106%);
}
</style>
