<template>
  <div class="container">
    <h3>Intérêts :</h3>
    <ul v-for="interest in interests" v-bind:key="interest.id">
      <interest-item :interest="interest"></interest-item>
    </ul>
  </div>
</template>

<script>
import interests from "./../../utility/interests.JSON";
import interestItem from "./interest-item.vue";

export default {
  name: "interests",
  data() {
    return {
      selectedInterests: [],
      interests: interests.interests,
    };
  },
  components: {
    interestItem,
  },
  methods: {
    addInterest(interest) {
      this.interests.find((data) => data.id == interest.id).state = true;
      this.selectedInterests.push(interest.id);
      this.$root.$refs.domainList.updateInterests(this.selectedInterests);
    },
    removeInterest(interest) {
      this.interests.find((data) => data.id == interest.id).state = false;
      this.selectedInterests = this.selectedInterests.filter(
        (data) => data != interest.id
      );
      this.interests.push(interest);
      this.$root.$refs.domainList.updateInterests(this.selectedInterests);
    },
    updateInterests() {
      this.selectedInterests.forEach((interest) => {
        this.interests.find((data) => data.id == interest).state = true;
      });
    },
  },
  mounted() {
    if (localStorage.interests) {
      this.selectedInterests = JSON.parse(localStorage.interests);
      this.interests.forEach((interest) => {
        if (this.selectedInterests.includes(interest.id)) {
          interest.state = true;
        }
      });
    }
  },
  created() {
    this.$root.$refs.interestList = this;
    this.selectedInterests = this.$root.$refs.domainList.interests;
    this.updateInterests();
  },
};
</script>

<style scoped>
h3 {
  margin: 40px 0px 40px 10px;
  text-align: left;
}

ul {
  padding-bottom: 10px;
}
</style>
