<template>
  <div class="interest">
    <input
      type="checkbox"
      class="checkbox"
      v-model="checked"
      @click="interestChecked()"
    />
    <label>{{ interest.name }}</label>
  </div>
</template>

<script>
export default {
  name: "interest",
  data() {
    return {
      checked: false,
    };
  },
  props: {
    interest: Object,
  },
  methods: {
    interestChecked() {
      if (!this.checked) {
        this.$root.$refs.interestList.addInterest(this.interest);
      } else {
        this.$root.$refs.interestList.removeInterest(this.interest);
      }
    },
  },
  created() {
    this.checked = this.interest.state;
  },
};
</script>

<style scoped>
.interest {
  display: flex;
}
label {
  font-size: 0.9em;
  margin-left: 10px;
}
</style>
