<template>
  <div class="conditionContainer">
    <h3>Affichage :</h3>
    <condition-item
      :condition="conditions[0]"
      class="displayAllButton"
      @click="showInterestsAndTypes()"
    />
    <div v-if="!conditions[0].state">
      <h3>Conditions de travail :</h3>
      <ul v-for="condition in conditions" v-bind:key="condition.id">
        <condition-item
          :condition="condition"
          v-if="condition.id != 0"
        ></condition-item>
      </ul>
    </div>
  </div>
</template>

<script>
import conditions from "./../..//utility/conditions.JSON";
import ConditionItem from "./condition-item.vue";

export default {
  data() {
    return {
      conditions: conditions.conditions,
      selectedConditions: [],
    };
  },
  components: {
    ConditionItem,
  },
  methods: {
    addCondition(condition) {
      this.conditions.find((data) => data.id == condition).state = true;
      this.selectedConditions.push(condition);
      this.$root.$refs.domainList.updateConditions(this.selectedConditions);
    },
    removeCondition(condition) {
      this.conditions.find((data) => data.id == condition).state = false;
      this.selectedConditions = this.selectedConditions.filter(
        (data) => data != condition
      );
      this.$root.$refs.domainList.updateConditions(this.selectedConditions);
    },
    updateConditions() {
      this.selectedConditions.forEach((condition) => {
        this.conditions.find((data) => data.id == condition).state = true;
      });
    },
    showInterestsAndTypes() {
      if (this.conditions[0].state) {
        this.$root.$refs.headerContent.showInterestsAndTypes = false;
      } else {
        this.$root.$refs.headerContent.showInterestsAndTypes = true;
      }
    },
  },
  mounted() {
    if (localStorage.conditions) {
      this.selectedConditions = JSON.parse(localStorage.conditions);
      this.conditions.forEach((condition) => {
        if (this.selectedConditions.includes(condition.id)) {
          condition.state = true;
        }
      });
    }
  },
  created() {
    this.$root.$refs.conditionList = this;
    this.selectedConditions = this.$root.$refs.domainList.conditions;
    this.updateConditions();
  },
};
</script>

<style scoped>
h3 {
  margin-top: 40px;
  margin-left: 10px;
  text-align: left;
}
.displayAllButton {
  margin-left: 10px;
}
ul:first-of-type {
  padding: 0;
}
</style>
