<template>
  <div>
    <div class="home">
      <Header-Content></Header-Content>
      <domain-list></domain-list>
    </div>
    <div v-if="showStartMenu">
      <modal-filters></modal-filters>
    </div>
  </div>
</template>

<script>
import HeaderContent from "./../components/Filters/header-content.vue";
import DomainList from "./../components/Content/domain-list.vue";
import ModalFilters from "./../components/StartMenu/modal-filters.vue";

export default {
  name: "Home",
  components: {
    HeaderContent,
    DomainList,
    ModalFilters,
  },
  data() {
    return {
      showStartMenu: true,
    };
  },
  mounted() {
    if (
      localStorage.conditions ||
      localStorage.interests ||
      localStorage.types
    ) {
      this.showStartMenu = false;
    }
  },
  created() {
    this.$root.$refs.home = this;
  },
};
</script>

<style scoped>
modal-filters {
  z-index: 1500;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0, 0, 0); /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}
</style>
