<template>
  <div class="condition">
    <input
      type="checkbox"
      class="checkbox"
      v-model="checked"
      @click="conditionChecked()"
    />
    <label @click="conditionChecked()">{{ condition.name }}</label>
  </div>
</template>

<script>
export default {
  name: "Condition",
  data() {
    return {
      checked: false,
    };
  },
  methods: {
    conditionChecked() {
      if (!this.checked) {
        this.checked = true;
        this.$root.$refs.conditionList.addCondition(this.condition.id);
        if (this.condition.id == 0) {
          this.$root.$refs.headerContent.showInterestsAndTypes = false;
        }
      } else {
        this.checked = false;
        this.$root.$refs.conditionList.removeCondition(this.condition.id);
        if (this.condition.id == 0) {
          this.$root.$refs.headerContent.showInterestsAndTypes = true;
        }
      }
    },
  },
  props: {
    condition: Object,
  },
  created() {
    this.checked = this.condition.state;
  },
};
</script>

<style scoped>
input {
  cursor: pointer;
}
.condition {
  display: flex;
}
label {
  font-size: 0.9em;
  margin-left: 10px;
  cursor: pointer;
}
</style>
