<template>
  <div>
    <h3>Afficher seulement:</h3>
    <div classs="checks">
      <ul v-for="typ in types" v-bind:key="typ.id">
        <sorting-item :type="typ"></sorting-item>
      </ul>
    </div>
  </div>
</template>

<script>
import types from "./../../utility/types.JSON";
import SortingItem from "./sorting-item.vue";

export default {
  data() {
    return {
      selectedTypes: [],
      types: types.types,
    };
  },
  methods: {
    addType(type) {
      this.types.find((data) => data.name == type).state = true;
      this.selectedTypes.push(type);
      this.$root.$refs.domainList.updateTypes(this.selectedTypes);
    },
    removeType(type) {
      this.types.find((data) => data.name == type).state = false;
      this.selectedTypes = this.selectedTypes.filter((data) => data != type);
      this.$root.$refs.domainList.updateTypes(this.selectedTypes);
    },
    updateTypes() {
      this.selectedTypes.forEach((type) => {
        this.types.find((data) => data.name == type).state = true;
      });
    },
  },
  components: {
    SortingItem,
  },
  mounted() {
    if (localStorage.types) {
      this.selectedTypes = JSON.parse(localStorage.types);
      this.types.forEach((type) => {
        if (this.selectedTypes.includes(type.name)) {
          type.state = true;
        }
      });
    }
  },
  created() {
    this.$root.$refs.sortingList = this;
    this.selectedTypes = this.$root.$refs.domainList.types;
    this.updateTypes();
  },
};
</script>

<style>
h3 {
  margin: 40px 0px 40px 10px;
  text-align: left;
}
select {
  width: 50%;
  height: auto;
  border: 1px solid var(--primary);
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 5px;
}
ul {
  list-style: none;
  padding: 10px;
}
.checks {
  margin-top: 20px;
}
</style>
