<template>
  <div>
    <ul>
      <transition-group name="slide-down" v-if="filteredDomains.length != 0">
        <li v-for="domain in filteredDomains" v-bind:key="domain.id">
          <domain-item :domain="domain" v-if="domain.jobs.length != 0" />
        </li>
      </transition-group>
      <div v-if="filteredJobs.length == 0">
        <h4>Aucun emploi correspondant aux filtres n'a été trouvé</h4>
      </div>
    </ul>
  </div>
</template>

<script>
import DomainItem from "./domain-item.vue";
import domains from "./../../utility/domains.JSON";
import jobs from "./../../utility/jobs.JSON";
import _ from "lodash";

export default {
  name: "DomainList",
  data() {
    return {
      domains: domains.domains,
      jobs: jobs.jobs,
      filteredDomains: [],
      filteredJobs: [],
      otherJobs: [],
      otherDomains: [],
      conditions: [],
      interests: [],
      types: [],
    };
  },
  components: {
    DomainItem,
  },
  methods: {
    updateConditions(conditions) {
      this.conditions = conditions;
      this.updateJobs();
    },
    updateInterests(interests) {
      this.interests = interests;
      this.updateJobs();
    },
    updateTypes(types) {
      this.types = types;
      this.updateJobs();
    },
    updateJobs() {
      this.filteredJobs = this.jobs;
      if (!this.conditions.includes(0)) {
        this.conditionStrictFilter();
      }
      if (this.interests.length > 0) {
        this.interestSuggestiveFilter();
      }
      if (this.types.length > 0) {
        this.typeFilter();
      }
      this.updateDomains();
    },
    updateDomains() {
      this.filteredDomains = this.domains;
      for (let i = 0; i < this.filteredDomains.length; i++) {
        this.filteredDomains[i].jobs = this.filteredJobs.filter(
          (job) => job.domain == this.filteredDomains[i].id
        );
      }
      this.updateOtherJobs();
    },
    updateOtherJobs() {
      this.otherJobs = _.difference(this.jobs, this.filteredJobs);
      // console.log(this.otherJobs);
      // this.otherDomains = this.domains;
      // for (let i = 0; i < this.otherDomains.length; i++) {
      //   this.otherDomains[i].jobs = this.otherJobs.filter(
      //     (job) => job.domain == this.otherDomains[i].id
      //   );
      // }
      // console.log(this.filteredDomains);
    },
    conditionStrictFilter() {
      this.filteredJobs = this.jobs.filter((job) => {
        let jobPassed = true;
        for (let i = 0; i < job.conditions.length; i++) {
          if (
            !(
              (this.verifyCondition(job, i) &&
                job.conditions[i].state == true) ||
              (!this.verifyCondition(job, i) &&
                job.conditions[i].state == false) ||
              job.conditions[i].state == null
            )
          ) {
            jobPassed = false;
          }
        }
        return jobPassed;
      });
    },
    verifyCondition(job, i) {
      return this.conditions.includes(job.conditions[i].id);
    },
    interestStrictFilter() {
      this.filteredJobs = this.filteredJobs.filter(
        (job) =>
          _.intersection(job.interests, this.interests).length ==
          this.interests.length
      );
    },
    conditionSuggestiveFilter() {
      this.filteredJobs = this.filteredJobs.filter(
        (job) => _.intersection(job.conditions, this.conditions).length > 0
      );
    },
    interestSuggestiveFilter() {
      this.filteredJobs = this.filteredJobs.filter(
        (job) => _.intersection(job.interests, this.interests).length > 0
      );
    },
    typeFilter() {
      this.filteredJobs = this.filteredJobs.filter(
        (job) => _.indexOf(this.types, job.name) > -1
      );
    },
  },

  mounted() {
    this.updateJobs();
    if (localStorage.conditions) {
      this.updateConditions(JSON.parse(localStorage.conditions));
    }
    if (localStorage.interests) {
      this.updateInterests(JSON.parse(localStorage.interests));
    }
    if (localStorage.types) {
      this.updateTypes(JSON.parse(localStorage.types));
    }
  },

  watch: {
    conditions(newValue) {
      localStorage.conditions = JSON.stringify(newValue);
    },
    interests(newValue) {
      localStorage.interests = JSON.stringify(newValue);
    },
    types(newValue) {
      localStorage.types = JSON.stringify(newValue);
    },
  },

  created() {
    this.$root.$refs.domainList = this;
  },
};
</script>

<style scoped>
.slide-down-enter-active,
.slide-down-move {
  transition: all 0.5s;
}
.slide-down-enter {
  transform: translateY(-10px);
  opacity: 0;
}
h2 {
  padding: 10px;
}
li {
  list-style: none;
}
</style>
