<template>
  <div class="container hero">
    <div class="containerHead">
      <h1>Cheminement de carrière</h1>
      <div class="buttonContainer">
        <button class="downloadButton" @click="uploadJsonFile()">
          <span>Téléverser</span>
          <img src="./../../assets/icon_upload-invert.svg" class="uploadImg" />
        </button>
        <button class="downloadButton" @click="showDownloadModal()">
          <span>Sauvegarder</span>
          <img src="./../../assets/icon_download-invert.svg" alt="" />
        </button>
        <button class="buttonInterrogation" @click="showStartMenu()">
          <img
            src="./../../assets/Icon_Interrogation.svg"
            class="interrogationButton"
          />
        </button>
      </div>
      <button @click="showFilters()" class="filterButton">
        <span>Filtres</span>
        <img
          src="./../../assets/Icon_arrow.svg"
          alt=""
          :class="{ active: showMenu }"
          class="arrow"
        />
      </button>
    </div>
    <download-modal v-if="showDownload"></download-modal>
    <nav class="menu" v-if="showMenu == true">
      <search-bar v-if="showSearchBar"></search-bar>
      <condition-list></condition-list>
      <interest-list v-if="showInterestsAndTypes"></interest-list>
      <sorting-list v-if="showInterestsAndTypes"></sorting-list>
    </nav>
  </div>
</template>

<script>
import ConditionList from "./condition-list.vue";
import InterestList from "./interest-list.vue";
import SearchBar from "./search-bar.vue";
import SortingList from "./sorting-list.vue";
import DownloadModal from "./download-modal.vue";

export default {
  name: "header-content",
  data() {
    return {
      showSearchBar: false,
      showMenu: false,
      showDownload: false,
      showInterestsAndTypes: true,
      showIcon: "+",
      jsonFilters: {},
    };
  },
  components: {
    InterestList,
    SearchBar,
    ConditionList,
    SortingList,
    DownloadModal,
  },
  methods: {
    showFilters() {
      this.showMenu = !this.showMenu;
    },
    showDownloadModal() {
      this.showDownload = !this.showDownload;
    },
    showStartMenu() {
      this.$root.$refs.home.showStartMenu = true;
    },
    uploadJsonFile() {
      let input = document.createElement("input");
      input.type = "file";
      input.addEventListener("change", (e) => {
        let file = e.target.files[0];
        let reader = new FileReader();
        reader.onload = (e) => {
          let data = JSON.parse(e.target.result);
          this.jsonFilters = data;
        };
        reader.readAsText(file);
      });
      input.click();
    },
  },
  watch: {
    jsonFilters() {
      localStorage.conditions = JSON.stringify(this.jsonFilters.conditions);
      localStorage.interests = JSON.stringify(this.jsonFilters.interests);
      localStorage.types = JSON.stringify(this.jsonFilters.types);
      this.$router.go();
    },
  },
  created() {
    this.$root.$refs.headerContent = this;
  },
};
</script>

<style scoped>
.buttonInterrogation {
  padding: 5px;
  margin: 10px;
  cursor: pointer;
}

.interrogationButton {
  filter: invert(99%) sepia(0%) saturate(7497%) hue-rotate(238deg)
    brightness(102%) contrast(101%);
  aspect-ratio: 1;
  width: 37px;
}

span {
  padding-right: 10px;
}
.buttonContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.containerHead {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
@media screen and (max-width: 1060px) {
  .menu {
    width: 100% !important;
  }
  .buttonContainer {
    flex-direction: column;
  }
  button {
    width: 300px !important;
  }
  .buttonInterrogation {
    order: -1 !important;
  }
}

.arrow {
  width: 15px;
  height: 15px;
  margin-left: 10px;
  transition: all 0.15s ease-in-out;
  filter: brightness(0) invert(1);
}
.active {
  transform: rotate(90deg);
}
.menu {
  display: flex;
  flex-direction: column;
  padding: 10px;
  background-color: var(--tertiary);
  width: 65%;
  margin: auto;
  top: 0;
  border-radius: 5px;
  margin-top: 20px;
}
.downloadButton {
  width: 180px;
  height: 50px;
  font-size: 1rem;
  font-weight: bold;
  margin: 10px;
  cursor: pointer;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.filterButton {
  font-size: 1.2em;
  font-weight: bold;
  cursor: pointer;
  border: none;
  transition: all 0.2s;
  height: 50px;
  margin: 10px;
  width: 447px;
  display: flex;
  justify-content: center;
  align-items: center;
}
h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  padding: 40px;
}
</style>
